<template>
	<view>
		<view class="" style="padding: 70rpx 30rpx;">
			<view class="image" style="width: 300rpx;height:300rpx;margin: 0 auto;">
				<u-image width="100%" height="300rpx" :src="src"></u-image>
			</view>
			<view class="" style="text-align: center;margin-top: 30rpx;">
				<text>版本：{{version}}</text>
			</view>
		</view>
		<!-- #ifdef APP-PLUS || H5 -->
		<view class="center-list-item" @tap='updataApp()'>
			<text class="list-text">检测更新</text>
			<uni-icons type="arrowright" size="34rpx" color="#333333"></uni-icons>
		</view>
		<!-- #endif -->
		<view class="center-list-item" @tap='enterPage(index)' v-for="(item,index) in menu" :key='index' hover-class="bgColor">
			<text class="list-text">{{item.title}}</text>
			<uni-icons type="arrowright" size="34rpx" color="#333333"></uni-icons>
		</view>
		<view class="componyMark">
			<view class="tip">
				<text @click="userAgree(0)">用户协议</text>
				<text style="margin: 0 20rpx;color: #DDDDDD;">|</text>
				<text @click="userAgree(1)">隐私政策</text>
			</view>
			<text>广州诺曼数字化医疗科技有限公司 版权所有</text>
		</view>
		<wechatShare ref='sonWechatShare' @shareWeixin='shareWeixin' @shareFriend='shareFriend' @cancelshare='cancelshare'></wechatShare>
	</view>
</template>

<script>
	import {update} from '@/utill/tools/update/checkUpdate.js'
	export default {
		data() {
			return {
				src:uni.getStorageSync('uploadLogo'),
				menu:[
					{title:'功能介绍'},
					{title:'投诉'},
					// #ifdef APP-PLUS
					{title:'分享ShoYii'},
					{title:'小程序'},
					{title:'发行页面'}
					// #endif
				],
				version:'1.0.0'
			}
		},
		methods: {
			updataApp(){
				plus.runtime.getProperty(plus.runtime.appid,(infor)=>{
					this.$myRequest({
						url:'/App/getDoctorAppLatestEdition',
					}).then(res=>{
					 console.log(res)
					 if(res.data.code==0){
						 var type=res.data.object.updateType//更新方式，1是整包更新 2是资源热更新
						 if(type=='1'){
							 var url=res.data.object.latestDoctorPkgDownloadUrl
						 }else{
							 var url=res.data.object.updateUrl//热更新
						 }
						 console.log(type,url)
						 if(infor.version<res.data.object.codeVersion){
							 uni.showModal({
								title:'新版发布',
								content:'发现重要版本，请马上升级',
								cancelText:'取消更新',
								confirmText:'确定更新',
								success(e){
									console.log(e)
									if(e.confirm){//确定下载	
									  update(type,url) //下载
									}
								},
								fail(e){
									console.log(e)
								}
							 })
						 }else{//最新版本
							uni.showToast({
								title:'当前为最新版本',
								icon:'none'
							}) 
						 }
					 }
					}).catch(err=>{
						console.log(err)
					})
				})
			},
			enterPage(index){
				if(index==0){//功能介绍
					uni.navigateTo({
						url:'/pages/aboutShoyii/introduce'
					})
				}else if(index==1){//投诉
					uni.navigateTo({
						url:'/pages/aboutShoyii/complain'
					})
				}
				// #ifdef APP-PLUS
				else if(index==2){//分享shoyii
					this.$refs.sonWechatShare.open()
				}else if(index==3){//进入小程序
					plus.share.getServices(function(res){  
						console.log(res)
						var sweixin = null;  
						for(var i=0;i<res.length;i++){  
							var t = res[i];  
							if(t.id == 'weixin'){  
								sweixin = t;  
							}  
						}  
						if(sweixin){  
							sweixin.launchMiniProgram({  
								id: 'gh_088556d792b9',  
								path:"/pages/index/index",
								type: 0   
							});  
						}  
					},function(res){  
						console.log(res)
						console.log(JSON.stringify(res));  
					});
				}else if(index==4){//发行页面
					uni.navigateTo({
						url:'/pages/ThreejsShow/ThreejsShow?baseUrl='+'http://m3w.cn/syys'
					})
				}
				// #endif
			},
			shareWeixin(){
				uni.share({
					provider:'weixin',
					title:'树蚁医生',
					type:0,
					href:this.$onlineUrl+'pages/download/download',
					summary:'树蚁医疗是一个方便医生进行精准外科三维数据展示与运用，从而关联XR影像系统，自研三维重建系统。真正帮助医生阅读精准三维诊断数据，精准外科学习与运用以及医患沟通',
					scene:'WXSceneSession',
					imageUrl:'/static/image/shoyiilogo.png',
					success(e) {
						uni.showToast({
							title:'分享成功',
							icon:'none'
						})
						console.log(e)	
					},
					fail(e) {
						uni.showToast({
							title:'分享失败',
							icon:'none'
						})
						console.log(e)
					}
				})
			},
			shareFriend(){
				uni.share({
					provider:'weixin',
					title:'树蚁医生',
					type:0,
					href: this.$onlineUrl+'pages/download/download',
					summary:'树蚁医疗是一个方便医生进行精准外科三维数据展示与运用，从而关联XR影像系统，自研三维重建系统。真正帮助医生阅读精准三维诊断数据，精准外科学习与运用以及医患沟通',
					scene:'WXSenceTimeline',
					imageUrl: '/static/image/shoyiilogo.png',
					success(e) {
						uni.showToast({
							title:'分享成功',
							icon:'none'
						})
						console.log(e)
					},
					fail(e) {
						uni.showToast({
							title:'分享失败',
							icon:'none'
						})
						console.log(e)
					}
				})
			},
			cancelshare(){
				this.$refs.sonWechatShare.close()
			},
			userAgree(index){
				if(index==0){//用户协议
					uni.navigateTo({
						url:'/pages/aboutShoyii/userAgree'
					})
				}else if(index==1){//隐私政策
					uni.navigateTo({
						url:'/pages/aboutShoyii/privacyPolicy'
					})
				}
			}
		},
		onLoad() {
			// #ifdef APP-PLUS
				plus.runtime.getProperty(plus.runtime.appid,(infor)=>{
					console.log(infor.version);
					this.version=infor.version
				})
			// #endif 
			// #ifdef MP-WEIXIN
			　　const accountInfo = wx.getAccountInfoSync();
			    // console.log(accountInfo)//miniProgram {appId: "wx347cba31a8aac486" envVersion: "develop"version: ""}
			　　this.version = accountInfo.miniProgram.version // 小程序 版本号
			// #endif
		}
	}
</script>

<style scoped>
	.center-list-item {
		box-sizing: border-box;
		padding: 30rpx 30rpx;
		border-bottom: 1px solid #EEEEEE;
		align-items: center;
		background-color: #FFFFFF;
		display: flex;
		justify-content: space-between;
	}
	.list-text {
		font-size: 30rpx;
		color: #000000;
		font-family: PingFang-SC-Regular;
		flex: 1;
		text-align: left;
	}
	.bgColor{
		background-color: #F8F8F8;
	}
	.componyMark{
		position: fixed;
		bottom: 20px;
		left: 0;
		right: 0;
		text-align: center;
		color: gray;
		font-size: 28rpx;
	}
	.tip{
		padding: 10rpx 0;
		color: #86B0D4;
		font-size: 24rpx;
	}
</style>
